import React, { Component } from 'react';

import './style/Username.sass';

const rankColors = [
  { dark: 'inherit', light: 'inherit' },
  { dark: '#206694', light: '#3498db' },
  { dark: '#c27c0e', light: '#f1c40f' },
];

export default class Username extends Component {
  render() {
    let { user, showColor, isLink, discriminator, ...props } = this.props;
    if (showColor) {
      const color = rankColors[user.permLevel];
      if (!props.style) props.style = {};
      props.style.color = color.light;
      if (!discriminator) discriminator = { style: {} };
      else if (!discriminator.style) discriminator.style = {};
      discriminator.style.color = color.dark;
    }

    if (isLink) {
      const href = `/profile/${ encodeURIComponent(user.username) }#${user.discriminator}`
      return (
        <a href={ href } { ...props }>{ user.username }<small { ...discriminator }>#{ user.discriminator }</small></a>
      );
    }
    return (
      <span { ...props }>{ user.username }<small { ...discriminator }>#{ user.discriminator }</small></span>
    );
  }
}