function showDate(date) {
  const now = Date.now();
  if (!(date instanceof Date)) date = new Date(date);
  const str = (
    (now - date > 86400000)
      ? (date.toLocaleString())
      : (date.toLocaleTimeString())
    ).replace(/:\d{1,2} /, ' ');

  return ( <time dateTime={ date.toISOString() }>{ str }</time> );
}

